.Contactcontainer {
    width: 91%;
    margin-left: 9vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@keyframes rollin {
    from{
        transform: translateY(-40vh) scale(2)  ;
        color:#0B132B ;
        text-shadow: 500px -500px 10px  #0B132B
         
    }
    to{
        transform: translateY(0) scale(1) ;
        color: #5BC0BE;
        text-shadow: 20px 20px 15px  #0B132B
    }
}

.Contactheader {
    position: relative;
    bottom: 5vh;
    width: 100%;
    text-align: center;
    font-family: Arimo;
    font-size: 4vw;
    color: #5BC0BE;
    text-shadow: 20px 20px 10px  #0B132B;
    -webkit-text-stroke: 1px #0B132B;
    text-decoration: 10px;
    padding-top: 5%;
    margin: 0;
    border: 0;
    transform: translateY(-40vh)  ;
    animation: rollin 1s forwards;
    
     
    
}

.Contactform {
    box-sizing: border-box;
    width: 90%;
    height: 80vh;
    padding: 0%; 
    border: 1px solid #6FFFE9;
    background-color: #1C2541;
    border-radius: 15px;
    box-shadow: 20px 20px 15px #0B132B;
    animation: contentrollin 1s forwards;   
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: scroll; 
    align-items: center;  
    font-family: Arimo;
    font-size: 3vh;

}
.Contactform::-webkit-scrollbar {
    display: none;
}

 

@media (max-width: 1050px) {
    .Contactheader {
        padding-top: 120px;
        font-size: 10vw;
        transform: translateY(0);
        animation: none;
        bottom: 0vh;
         
    }
    .Contactcontainer {
        width: 100%;
        margin-left: 0vw;
    }
    .Contactform {
         
        width: 95%;
        color: #5BC0BE;
        justify-content: start;
        height: 70vh;
        animation:none;
        font-size: 2vh;
    }
     
}